// src/app/blogs/[slug]/page.tsx
import { notFound } from 'next/navigation';
import { post1 } from '@/components/blogs/p1';
import { post2 } from '@/components/blogs/p2';
import { post3 } from '@/components/blogs/p3';
import BlogPostHeader from '@/components/BlogPostHeader';
import BlogPostContent from '@/components/BlogPostContent';
import RelatedArticles from '@/components/RelatedArticles';

// ÂÆö‰πâÂçöÂÆ¢ÊñáÁ´†Á±ªÂûã
interface BlogPost {
  id: number;
  slug: string;
  title: string;
  description: string;
  content: string;
  date?: string; // Ê∑ªÂä†ÂèØÈÄâÊó•ÊúüÂ±ûÊÄß
  author?: string; // Ê∑ªÂä†ÂèØÈÄâ‰ΩúËÄÖÂ±ûÊÄß
  readTime?: string; // Ê∑ªÂä†ÂèØÈÄâÈòÖËØªÊó∂Èó¥Â±ûÊÄß
}

// Â∞ÜÊâÄÊúâÂçöÂÆ¢ÊñáÁ´†ÂêàÂπ∂Âà∞‰∏Ä‰∏™Êï∞ÁªÑÂπ∂Ê∑ªÂä†Á±ªÂûã
const blogPosts: BlogPost[] = [
  { ...post1, date: "2025-10-10", author: "Perfect Tidy Team", readTime: "5 min read" },
  { ...post2, date: "2025-10-20", author: "Community Expert", readTime: "8 min read" },
  { ...post3, date: "2025-10-31", author: "Game Master", readTime: "6 min read" }
];

// Ë∞ÉËØïÂáΩÊï∞ÔºöÊâìÂç∞ÊâÄÊúâÊñáÁ´†ÁöÑslug
function debugSlugs() {
  console.log("Available slugs:");
  blogPosts.forEach(post => {
    console.log(`- ${post.slug}`);
  });
}

// Âä®ÊÄÅÈ°µÈù¢ÁªÑ‰ª∂
export default function BlogPost({ params }: { params: { slug: string } }) {
  // Ëß£Á†ÅURL‰∏≠ÁöÑslugÔºàÂ§ÑÁêÜÁâπÊÆäÂ≠óÁ¨¶ÂíåÁ©∫Ê†ºÔºâ
  const decodedSlug = decodeURIComponent(params.slug);
  
  // Êü•ÊâæÂåπÈÖçÁöÑÊñáÁ´†Ôºà‰∏çÂå∫ÂàÜÂ§ßÂ∞èÂÜôÂíåËøûÂ≠óÁ¨¶Ê†ºÂºèÔºâ
  const post = blogPosts.find(p => 
    p.slug.toLowerCase().replace(/\s+/g, '-') === decodedSlug.toLowerCase().replace(/\s+/g, '-')
  );

  // Â¶ÇÊûúÊ≤°ÊúâÊâæÂà∞ÂØπÂ∫îÁöÑÊñáÁ´†ÔºåËøîÂõû 404 È°µÈù¢
  if (!post) {
    debugSlugs(); // ÊâìÂç∞ÂèØÁî®ÁöÑslugÁî®‰∫éË∞ÉËØï
    console.log("Requested slug:", decodedSlug);
    notFound();
  }

  return (
    <div className="min-h-screen bg-[#f0f4f8] py-12">
      <div className="max-w-4xl mx-auto px-4">
        <BlogPostHeader 
          title={post.title} 
          author={post.author || "Unknown Author"} 
          date={post.date || "Unknown Date"} 
          readTime={post.readTime || "Unknown"} 
        />
        <BlogPostContent content={post.content} />
        <RelatedArticles postSlug={post.slug} blogPosts={blogPosts} />
      </div>
    </div>
  );
}

// ÁîüÊàêÈùôÊÄÅË∑ØÂæÑ
export async function generateStaticParams() {
  // Á°Æ‰øù blogPosts ‰∏ç‰∏∫Á©∫
  if (!blogPosts || blogPosts.length === 0) {
    throw new Error("Ê≤°ÊúâÊâæÂà∞ÂçöÂÆ¢ÊñáÁ´†");
  }

  // ÁîüÊàêÊâÄÊúâÂä®ÊÄÅË∑ØÂæÑ
  return blogPosts.map((post) => ({
    slug: post.slug,
  }));
}

// Ê†πÊçÆÂä®ÊÄÅ slug ÁîüÊàêÂÖÉÊï∞ÊçÆÔºàSEOÔºâ
export async function generateMetadata({ params }: { params: { slug: string } }) {
  // Ê£ÄÊü• slug ÊòØÂê¶Â≠òÂú®
  if (!params.slug) {
    console.error("Slug not found in params");
    return {
      title: "Post Not Found",
      description: "Sorry, we couldn't find the post you were looking for.",
    };
  }

  // Ëß£Á†ÅURL‰∏≠ÁöÑslug
  const decodedSlug = decodeURIComponent(params.slug);

  // Êü•ÊâæÂåπÈÖçÁöÑÊñáÁ´†
  const post = blogPosts.find((p) => p.slug === decodedSlug);

  // Â¶ÇÊûúÊ≤°ÊúâÊâæÂà∞ÊñáÁ´†ÔºåËøîÂõû 404 È°µÈù¢ÂÖÉÊï∞ÊçÆ
  if (!post) {
    return {
      title: "Post Not Found",
      description: "Sorry, we couldn't find the post you were looking for.",
    };
  }

  // Â¶ÇÊûúÊâæÂà∞ÊñáÁ´†ÔºåËøîÂõû SEO Êï∞ÊçÆ
  return {
    title: `${post.title}`,
    description: post.description 
      ? post.description 
      : post.content.replace(/<[^>]*>/g, '').substring(0, 160),
  };
}